.contact-edit {
  background-color: $clr-base-5;
  height: 100%;
  padding: 1.5em;
  border-radius: 1.5em;

  h2 {
    margin-bottom: 1rem;
  }

  .form-edit {
    display: flex;
    flex-direction: column;
    max-width: 400px;

    label {
      font-size: rem(16px);
      margin-bottom: 0.5em;
    }
    input {
      height: 3rem;
      padding: 1rem;
      border-radius: 6px;
      border: 1px solid $clr-base-3;
      background-color: $clr-base-4;
      margin-bottom: 1.5em;
      color: $clr-base-1;
      font-size: 1rem;

      &:focus-visible {
        outline: none;
      }
    }

    button {
      height: 3rem;
      padding: 1rem;
      border-radius: 1000px;
      background-color: $clr-base-2;
      font-size: 1rem;
      width: 200px;
    }
  }
}